	module.exports = {"data":{"markdownRemark":{"html":"<p><em>Written for Bandflare.com</em></p>\n<p>If you have gone to an Open Mic, you can agree with their usefulness as a tool for musicians.\nHere are some reasons to start/continue going:</p>\n<ol>\n<li>Performance Experience</li>\n<li>Pilot New Material</li>\n<li>Ensure Engagement Consistency</li>\n<li>Networking/Bootstrapping</li>\n</ol>\n<h1>Performance Experience</h1>\n<p>This is perhaps the most obvious benefit of attending an Open Mic.\nA lot of performance hinges upon comfort.\nComfort is built through experience.</p>\n<p>Things go wrong, a lot.\nExperiencing, learning from, and overcoming common mistakes builds comfort and professionalism.\nSince Open Mic nights are such low pressure, they are a great place to make mistakes.</p>\n<p>Here are just a handful of things I have experienced within the safety of Open Mic, sets:</p>\n<ol>\n<li>forgetting words</li>\n<li>going out of tune</li>\n<li>breaking strings</li>\n<li>loud feedback</li>\n<li>low audience engagement</li>\n<li>poor sound quality</li>\n</ol>\n<h1>Pilot New Material</h1>\n<p>In addition to experience, comfort is also built through repetition.\nPracticing at home and live are completely different.\nOpen Mic's serve as my bridge between the two worlds.</p>\n<p>Use Open Mic's to test and rehearse material.\nThen when the stakes are high, pull out the show stoppers and old reliable songs.</p>\n<p>Try to figure out which songs work with the crowd.\nLearn how crowds respond to sets differently.\nPlay with sequencing, and talking between songs.</p>\n<h1>Ensure Engagement Consistency</h1>\n<p>Open Mic's provide external benefits in addition to the internal ones explored above.\nUse them to engage with your fanbase.\nIf you play an Open Mic night once a week (you should), you get these items for free:</p>\n<ol>\n<li>reason email subscribers</li>\n<li>interesting content to post to social media (an event, reminder etc.)</li>\n<li>new photos for social media</li>\n<li>new recording of a performance (why not record/release these performances)</li>\n</ol>\n<h1>Networking/Bootstrapping</h1>\n<p>Never expect to gain fans from playing Open Mic nights.\nMaybe you will get lucky, but the odds are low.\nHowever, networking with the other musicians is usually fruitful.</p>\n<p>This the information other players have is valuable:</p>\n<ol>\n<li>other musicians to play with</li>\n<li>other open mic nights that are good</li>\n<li>local venues to play</li>\n<li>local studios to record at</li>\n</ol>\n<p>Feel free to discuss your thoughts and opinions on the subject in the comments section below.</p>","frontmatter":{"title":"Thoughts On Open Mic Nights"},"fields":{"date":"2016-04-11"}}},"pathContext":{"url":"/posts/2016-04-11-thoughts-on-open-mic-nights/"}}

